export default {
	name: 'UniBadge',
	props: {
		type: {
			type: String,
			default: 'default'
		},
		inverted: {
			type: Boolean,
			default: false
		},
		text: {
			type: [String,Number],
			default: ''
		},
		size: {
			// small.normal
			type: String,
			default: 'normal'
		}
	},
	data() {
		return {
			width: `display: inline-block;width: ${String(this.text).length * 15 + 25}rpx`
		};
	},
	methods: {
		onClick() {
			this.$emit('click');
		}
	}
};
